<template>
    <div class="video-container" v-if="isTopView">
        <a-card :bordered="false" class="ant-pro-components-tag-select">
            <a-form :form="form" layout="inline">
                <a-form-item>
                    <a-input-search
                        style="margin-bottom: 8px; width: 400px"
                        placeholder="精确查询：输入设备唯一编码"
                        enter-button
                        allow-clear
                        @search="onChange"
                    />
                </a-form-item>
            </a-form>
        </a-card>

        <div class="ant-pro-pages-list-projects-cardList">
            <a-list
                :loading="loading"
                :data-source="videos"
                :pagination="true"
                :grid="{ gutter: 24, xl: 4, lg: 3, md: 3, sm: 2, xs: 1 }"
            >
                <a-list-item slot="renderItem" slot-scope="item">
                    <a-card
                        class="ant-pro-pages-list-projects-card"
                        hoverable
                        @click="jumpPlay(item.main_id, item.play_url)"
                    >
                        <!-- <router-link :to="`/videos/${item.main_id}/play`">
                        </router-link>-->
                        <img
                            slot="cover"
                            v-if="item.cover"
                            v-lazy="item.cover"
                            :alt="item.main_id"
                            class="cover"
                        />
                        <img
                            slot="cover"
                            v-else
                            :src="defaultCover"
                            :alt="item.main_id"
                            class="cover"
                        />
                        <a-card-meta :title="item.device_id + '-(' + item.main_id + ')'">
                            <template slot="description">
                                <ellipsis
                                    :length="30"
                                >{{ item.channel_name }}({{ item.channel_id }})</ellipsis>
                            </template>
                        </a-card-meta>
                    </a-card>
                </a-list-item>
            </a-list>
        </div>
    </div>
    <router-view v-else />
</template>

<script>
// TODO:  add timer 30
import PermissionButton from '@/components/PermissionButton.vue'
import { videoOnlineList, videoSrc } from '@/api/device'
import { Ellipsis } from '@/components'
import moment from 'moment'
import { getRouteByCode } from '@/libs/utils'

export default {
    components: {
        Ellipsis,
        PermissionButton
    },
    data() {
        return {
            timer: null,
            defaultCover: require('@/images/none-cover.png'),
            queryParams: {
                mainId: ''
            },
            videos: [],
            videoCovers: [],
            form: this.$form.createForm(this),
            loading: false,
            hasVideoPlayPermission: false
        }
    },
    computed: {
        isTopView() {
            return this.$route.path === '/device/videos'
        }
    },
    filters: {
        fromNow(date) {
            return moment(date).fromNow()
        }
    },
    mounted() {
        if (this.isTopView) {
            this.getList()
            this.checkPermissions()
            this.timingTask()
        }
    },
    beforeDestroy() {
        this.clearTimer()
    },
    methods: {
        checkPermissions() {
            const vueRouters = this.$store.state.vueRouters.vueRouters
            const route = getRouteByCode(vueRouters, 'videos-play')
            if (route) {
                this.hasVideoPlayPermission = true
            }
        },
        clearTimer() {
            if (this.timer) {
                clearInterval(this.timer)
            }
        },
        timingTask() {
            this.clearTimer()
            this.timer = setInterval(() => {
                this.getList()
            }, 1000 * 60 * 1)
        },
        jumpPlay(mainId, playUrls) {
            if (this.hasVideoPlayPermission) {
                this.$router.push({ path: `/videos/${mainId}/play`, query: { mainId: mainId, playUrls: playUrls } })
            }
        },
        onChange(value) {
            this.queryParams.mainId = value
            this.getList()
        },
        handleChange(value) {
            console.log(`selected ${value}`)
        },
        sleep(numberMillis) {
            let now = new Date()
            const exitTime = now.getTime() + numberMillis
            while (true) {
                now = new Date()
                if (now.getTime() > exitTime) return
            }
        },
        async getList() {
            this.loading = true
            const videos = await videoOnlineList(this.queryParams)
            this.videos = videos
            this.loading = false
            setTimeout(() => {
              this.getVideoCovers()
            }, 1000)
        },
        async getVideoCovers() {
            for (const index in this.videos) {
                const video = this.videos[index]
                const { cover } = await videoSrc(video.media_server_id, video.play_url[3])
                video.cover = cover
                this.$set(this.videos, index, video)
                // this.sleep(2000)
            }
        }
    }
}
</script>

<style lang="less" scoped>
.cover {
    max-height: 200px;
}
</style>
